.analitycs {
    position: relative;
    z-index: 3;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  color: black;
    width: 100%;
    height: 100%;
 
}

.analitycs .aHeader{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
}

.analitycs .aHeader :first-child {
    color: #810551;
}

.analitycs .aHeader :nth-child(2){
    color: #05454d;
}